@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  background-color: #121018;
  color: #6F6994;
  font-size: 24px;
  font-family: 'Roboto Mono', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.font-krona-one {
  font-family: 'Krona One', sans-serif;
  color: #fff;
}

.font-montserrat {
  font-family: 'Montserrat', sans-serif;
  color: #fff;
  font-size: 20px;
}

@media (min-width: 1024px) {
    .main-background {
        background-image: url("https://firebasestorage.googleapis.com/v0/b/design-portfolio-53001.appspot.com/o/Assets%2FHome%2FBackground%2FMain%20Background.png?alt=media&token=aef14f00-49fe-476a-906c-e6a02eb92f51");
        background-repeat: no-repeat;
        background-position: right center;
    }
}
.textbox:focus {
    outline: none;
}
.textbox {
    border: none;
    padding: 0 1rem;
    height: 100%;
    background-color: transparent;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    -webkit-flex: 1 1;
            flex: 1 1
}
.textbox-wrapper {
    overflow: hidden;
    background: rgba(196, 196, 196, 0.04);
    border-radius: 10px;
    box-sizing: border-box;
    border: 1px solid #6F6994;
    /* width: 487px; */
    height: 65px;
    left: 477px;
    top: 450px;   
}
.textbox-wrapper.loginFailed {
    border-color: rgba(239, 68, 68, 1);
}

.view-img {
    display: block;
    object-fit: contain;
    width: 22px;
    margin: 0 10px;
    cursor: pointer;
}
