.textbox:focus {
    outline: none;
}
.textbox {
    border: none;
    padding: 0 1rem;
    height: 100%;
    background-color: transparent;
    flex-grow: 1;
    flex: 1
}
.textbox-wrapper {
    overflow: hidden;
    background: rgba(196, 196, 196, 0.04);
    border-radius: 10px;
    box-sizing: border-box;
    border: 1px solid #6F6994;
    /* width: 487px; */
    height: 65px;
    left: 477px;
    top: 450px;   
}
.textbox-wrapper.loginFailed {
    border-color: rgba(239, 68, 68, 1);
}

.view-img {
    display: block;
    object-fit: contain;
    width: 22px;
    margin: 0 10px;
    cursor: pointer;
}